import React, { useEffect, useRef } from 'react';
import { connect } from 'react-redux';
import { Text } from '@sitecore-jss/sitecore-jss-react';
import { createStructuredSelector } from 'reselect';
import { withNamespaces } from 'react-i18next';
import { get } from 'lodash';
import { withSitecoreContext } from '@sitecore-jss/sitecore-jss-react';
import { canUseDOM } from '../../../utils/canUseDOM';
import { setEqualHeight } from '../../../utils/setEqualHeight';

import { selectedFiltersData, filteredData, filtersData } from '../Filters/redux/selectors';
import useStyles from './style';
import {
  updateFiltersData,
  getFilterdInfoData,
} from '../Filters/redux/actions';
import DctSvgIcon from '../../../components/atoms/Svg';
import Grid from '../../../components/molecules/Grid';
import Typography from '../../../components/atoms/Typography';
import { getHelperKeysFromDictionary } from '../../../../utils/utility';
import { dictionaryKeys } from './constants';
import withErrorBoundary from '../../../utils/HOCs/withErrorBoundary';
import CarouselCard from '../../../components/molecules/CarouselCard';
import {
  FILTERED_LIST_CONTAINER,
  SELECTED_FILTERE_CONTAINER,
} from './locators';
import ButtonMUI from '../../../components/atoms/Button';
import clsx from 'clsx';
import {
  pushAnaylyticsData,
  createDynamicLabels,
} from '../../../../utils/analytics';
import { getPriceRange } from '../../../utils/getPriceRange';
import { analyticsData } from '../../../../constants/Analytics/analytics-data';
import { EVENT_TYPE, LISTING_TYPE, TAGS } from '../../../../constants/index';
import { getCardCategoryTags } from '../../../utils/getCardCategoryTags';
import { getRedirectionUrl } from '../../../utils/getRedirectionUrl';
import { getCurrentLanguage } from '../../../utils/getCurrentLanguage';
import { getPageName } from '../../../utils/getPageComponentInfo';
import { getDictionaryText } from '../../../utils/getDictionaryText';

const FilteredList = props => {
  let contentType = get(
    props,
    'sitecoreContext.route.fields.contentType.fields.taxonomyKey.value',
    ''
  ).toLowerCase();
  const templateName = get(props, 'sitecoreContext.route.templateName', '');
  // this key is used to handle global search cases
  const isGlobalSearchTemplate = templateName === 'GlobalSearchResult';

  const carouselCardWrapper = useRef();

  const addAnalyticsInfo = () => {
    const title = get(
      props,
      'sitecoreContext.route.fields.pageHeading.value',
      ''
    );
    const dynamicLabel = `${title}-${get(
      analyticsData,
      'pageBodyInteraction.loadMoreBtnClick.action'
    )}`;
    const obj = {
      ...get(analyticsData, 'pageBodyInteraction.loadMoreBtnClick'),
      ...{
        label: createDynamicLabels([
          getPageName(props),
          props.componentName,
          dynamicLabel,
        ]),
      },
    };
    pushAnaylyticsData(obj);
  };
  useEffect(() => {
    if (canUseDOM) {
      const lazyloadedImages = document.querySelectorAll('.lazyloaded');
      lazyloadedImages &&
        lazyloadedImages.forEach(img => {
          img.classList.remove('lazyloaded');
          img.classList.add('lazyload');
        });
      // Make sure address aligns with it self throughtout cards.
      setEqualHeight(carouselCardWrapper.current, '.bot-area');
    }
  });
  useEffect(() => {
    if (canUseDOM) {
      window.addEventListener('resize', () => {
        // Make sure address aligns with it self throughtout cards.
        setEqualHeight(carouselCardWrapper.current, '.bot-area');
      });
    }
  }, []);
  const classes = useStyles();
  const dictionary = props && props.t;
  const { appConfig = {} } = props;
  const ratingOutOf = dictionary('outOf5');
  const language = getCurrentLanguage(props);
  const helperKeys = getHelperKeysFromDictionary(dictionary, dictionaryKeys);
  const {
    showingText,
    resultsText,
    loadMoreText,
    noResultFoundKey,
    multiResultFoundKey,
    singleResultFoundKey,
  } = helperKeys;
  const { searchTerm, isAttributeListingPage, sitecoreContext } = props;
  if (!get(props, 'filteredData.data.results')) {
    return '';
  }
  const { results, totalResults } = get(props, 'filteredData.data', {});
  const { payload = {} } = props.filteredData;
  const resultsLength = results && results.length;
  const resultFoundKey =
    resultsLength > 1 ? multiResultFoundKey : singleResultFoundKey;
  let defaultGooglePlaceId = sitecoreContext &&
    sitecoreContext.route &&
    sitecoreContext.route.fields &&
    sitecoreContext.route.fields.googlePlaceId;

  const renderText = (label, variant) => {
    return (
      <Typography variant={variant} gutterBottom={false}>
        <Text field={label} />
      </Typography>
    );
  };

  // selecting statement to show after receiveing results for different scenarios
  let displayResultsText = '';
  if (results && results.length === 0) {
    displayResultsText = (
      <div className={classes.numberResultFoundText}>
        {renderText(noResultFoundKey, 'h5')}
      </div>
    );
  } else if (isGlobalSearchTemplate) {
    displayResultsText = (
      <div
        className={clsx(classes.numberResultFoundText, classes.resultSpacing)}
      >
        {renderText(
          {
            value:
              resultFoundKey &&
              resultFoundKey.value &&
              resultFoundKey.value.split('{0}')[0],
          },
          'h5'
        )}
        {renderText({ value: totalResults }, 'h5Bold')}
        {renderText(
          {
            value:
              resultFoundKey &&
              resultFoundKey.value &&
              resultFoundKey.value.split('{0}')[1],
          },
          'h5'
        )}
        {renderText({ value: ` "${searchTerm}"` }, 'h5Bold')}
      </div>
    );
  } else {
    displayResultsText = (
      <div className={classes.numberResultFoundText}>
        {renderText(showingText, 'h5')}
        {renderText({ value: ` ${totalResults} ` }, 'h5Bold')}
        {renderText(resultsText, 'h5')}
      </div>
    );
  }

  const getAdditionalCardAttributes = item => {
    let other = {};
    let featuredEvent;
    if (contentType === EVENT_TYPE || item.contentType === EVENT_TYPE) {
      if (item.isFeatured) {
        featuredEvent = dictionary('featuredText') || '';
      }
      other = {
        startDate: item.startDate,
        endDate: item.endDate,
        openTime: item.openTime,
        closeTime: item.closeTime,
        umbrellaEventCount: item.umbrellaEventCount,
        isEventListing: true,
        peopleInterested:
          item.interestedCount &&
          `${item.interestedCount} ${dictionary('interestedText')}`,
        eventType: {
          featured: featuredEvent,
          isPaid: item.eventType,
        },
        locationTitle:
          item.location_Titles &&
          item.location_Titles.length > 0 &&
          item.location_Titles[0],
        region:
          item.location_Regions &&
          item.location_Regions.length > 0 &&
          item.location_Regions[0],
        language,
        urlSlug: item.urlSlug,
      };
    }
    return other;
  };

  const onLoadMoreClick = () => {
    addAnalyticsInfo();
    let apiPayload = payload;
    apiPayload.payload.offset = resultsLength;
    //For attribute listing page load more card limits are driven by sitecore,
    apiPayload.payload.limit = isAttributeListingPage
      ? Number(getDictionaryText('attributeLoadMoreCount'))
      : 6;
    apiPayload.isLoadMoreClicked = true;
    props.getFilterdInfoData(apiPayload);
  };

  const onFilterRemove = event => {
    const target = {
      name: event.target.name,
      value: event.target.displayValue,
      checked: event.target.checked,
    };
    props.updateFiltersData(target);
  };

  const getFilterDisplayText = (key) => {
    const filtersData = get(props, 'filtersData.data', '');
    if (filtersData && filtersData.length) {
      for (let filter of filtersData) {
        if (filter.listItems && filter.listItems.length) {
          for (let listItem of filter.listItems) {
            if (listItem.groupheading) {
              if (listItem.groupheading.key === key) {
                return listItem.groupheading.value;
              } else if (listItem.groupItems && listItem.groupItems.length) {
                for (let groupItem of listItem.groupItems) {
                  if (groupItem.key.value === key) {
                    return groupItem.displayText.value;
                  }
                }
              }
            } else if (listItem.fields) {
              if (listItem.fields.key.value === key) {
                // Handle Trip Advisor Rating Tags Display Text
                if (filter.fieldName.value === 'tripadvisorrating') {
                  let filterKey = listItem.fields.key.value;
                  if (filterKey === '[0 TO 5]') {
                    return getDictionaryText('anyRating');
                  } else if (filterKey === '5') {
                    return '●●●●●';
                  } else if (filterKey === '[4 TO 5]') {
                    return '●●●●○' + ' ' + getDictionaryText('andUp');
                  } else if (filterKey === '[3 TO 5]') {
                    return '●●●○○' + ' ' + getDictionaryText('andUp');
                  }
                } else {
                  return listItem.fields.displayText.value;
                }
              }
            }
          }
        }
      }
      return key;
    } else {
      return key;
    }
  }

  const renderFilters = selectedFilters => {
    let filtersDOM = [];
    Object.keys(selectedFilters).forEach(key => {
      const value = selectedFilters[key];

      if (Array.isArray(value) && value.length > 0) {
        for (let val of value) {
          const displayValue = getFilterDisplayText(val);
          let data = {};
          data = {
            target: {
              name: key,
              value: displayValue,
              displayValue: val,
              checked: false,
            },
          };
          filtersDOM.push(
            <div className={classes.filters}>
              {renderText({ value: displayValue }, 'labelBlack')}
              <DctSvgIcon
                name="CloseIcon"
                className={classes.crossIcon}
                id={`${key}_${displayValue}`}
                onClick={() => onFilterRemove(data)}
              />
            </div>
          );
        }
      } else if (value && !Array.isArray(value)) {
        const displayValue = getFilterDisplayText(value);
        let data = {};
        data = {
          target: {
            name: key,
            value: '',
            displayValue: '',
            checked: false,
          },
        };
        filtersDOM.push(
          <div className={classes.filters}>
            {renderText({ value: displayValue }, 'labelBlack')}
            <DctSvgIcon
              name="CloseIcon"
              className={classes.crossIcon}
              id={`${key}_${displayValue}`}
              onClick={() => onFilterRemove(data)}
            />
          </div>
        );
      }
    });

    return filtersDOM;
  };

  const renderResults = results => {
    let keys =
      props &&
      props.t &&
      props.t(contentType) &&
      props.t(contentType && contentType.toLowerCase()) &&
      props.t(contentType.toLowerCase()).split(',');
    let tags;
    return (
      results &&
      results.map((item, i) => {
        let priceTagData = getPriceRange(item.priceTagsKeys);
        let itemContentType = get(item, 'contentType', '').toLowerCase();
        if (
          (isGlobalSearchTemplate || isAttributeListingPage) &&
          itemContentType
        ) {
          /*
           * In case of GlobalSearchTemplate we extract contentType from API response
           * and show different tags for different categories of results
           */
          tags = getCardCategoryTags(
            item,
            dictionary(`${itemContentType}${TAGS}`)
          );
        } else {
          tags = getCardCategoryTags(item, dictionary(`${contentType}${TAGS}`));
        }
        let dymnamicProps = [];
        if (keys && keys.length > 0) {
          keys.forEach(key => {
            dymnamicProps[key] = get(item, key, '');
          });
        }
        const thumbnailSrc = {
          src: get(item, 'thumbnailImage', ''),
          alt: get(item, 'name', ''),
        };
        let checkContentType = contentType;
        if (isGlobalSearchTemplate || isAttributeListingPage) {
          checkContentType = itemContentType;
        }
        const thumbnailImage = {
          value:
            checkContentType === EVENT_TYPE && get(item, 'itemUniqueKey', '') !== dictionary('itemUniqueKeyEventListing')
              ? thumbnailSrc
              : get(item, 'thumbnailImage', ''),
        };

        return (
          <Grid
            item
            lg={isGlobalSearchTemplate ? 12 : 4}
            key={i}
            className={clsx(isGlobalSearchTemplate && classes.horizontalCard)}
          >
            <CarouselCard
              title={get(item, 'name', '')}
              desktopImage={thumbnailImage}
              mobileImage={thumbnailImage}
              aspectRatioNeeded={contentType !== EVENT_TYPE}
              imageCtaUrl={
                contentType === EVENT_TYPE || itemContentType === EVENT_TYPE
                  ? getRedirectionUrl(item, language)
                  : get(item, 'itemUrl', '')
              }
              ctaLink={tags}
              cardType={
                isGlobalSearchTemplate
                  ? 'searchResult'
                  : contentType === EVENT_TYPE || itemContentType === EVENT_TYPE
                    ? EVENT_TYPE
                    : LISTING_TYPE
              }
              description={
                isGlobalSearchTemplate && get(item, 'description', '')
              }
              ratingOutOf={ratingOutOf}
              pageName={getPageName(props)}
              componentName={props.componentName}
              {...dymnamicProps}
              {...getAdditionalCardAttributes(item)}
              highlightWords={searchTerm}
              expenseRange={priceTagData}
              viewsCount={get(item, 'viewsCount', '')}
              num_reviews={get(item, 'tripAdvisorNumberOfReviews', '')}
              rating_image_url={get(item, 'tripAdvisorRatingImageURL', '')}
              googlePlaceId={get(item, 'googlePlaceId', '')}
              defaultGooglePlaceId={defaultGooglePlaceId}
              language={language}
              appConfig={appConfig}
              forcedRatio={contentType === EVENT_TYPE || itemContentType === EVENT_TYPE ? '3x2' : null}
            />
          </Grid>
        );
      })
    );
  };

  return (
    <div
      className={classes.resultSection}
      data-locators={FILTERED_LIST_CONTAINER}
    >
      {displayResultsText}
      <div
        className={clsx(
          classes.sectectedFilterSection,
          isGlobalSearchTemplate && classes.resultSpacing
        )}
        data-locator={SELECTED_FILTERE_CONTAINER}
      >
        {renderFilters(get(props, 'selectedFiltersData.data'), {})}
      </div>
      <Grid
        container
        classes={{ root: classes.spacing }}
        className={clsx(isGlobalSearchTemplate && classes.resetSpacing)}
        ref={carouselCardWrapper}
      >
        {renderResults(results)}
      </Grid>
      {totalResults > resultsLength && totalResults > 9 && (
        <Grid item className={classes.loadMoreSection}>
          <ButtonMUI
            buttonType={'form'}
            type={'button'}
            onClick={() => onLoadMoreClick()}
            hasBorder={false}
            field={{ value: { text: renderText(loadMoreText, 'body2Bold') } }}
            iconOnly={false}
            iconName={'ExpandMoreIcon'}
            className={contentType === EVENT_TYPE && classes.eventBtnColor}
          />
        </Grid>
      )}
    </div>
  );
};

export const mapStateToProps = createStructuredSelector({
  selectedFiltersData: selectedFiltersData,
  filteredData: filteredData,
  filtersData: filtersData,
});

export const mapDispatchToProps = dispatch => {
  return {
    updateFiltersData: params => dispatch(updateFiltersData(params)),
    getFilterdInfoData: params => dispatch(getFilterdInfoData(params)),
  };
};

export default withErrorBoundary(
  withSitecoreContext()(
    withNamespaces()(connect(mapStateToProps, mapDispatchToProps)(FilteredList))
  )
);
export { FilteredList as FilteredListTest };
